import React, { Component } from 'react';

import chargeMarkerImg from '../markers/charge-marker.png';
import Popup from './Popup';

const styles = {
        popup: {
            position: "absolute",
            top: "-168px",
            width: "100px",
            height: "100px",
            backgroundColor: "lightgrey",
            zIndex: "1000",
        },
    };

class Marker extends Component {
    state = {
        open: false,
    };

    togglePopup = () => {
        const { open } = this.state;

        const logState = () => console.log(this.state);

        this.setState({ open: !open });
    };

  render() {
    const { id, info } = this.props;

      return (
        <div key={id} onClick={this.togglePopup}>
          <img src={chargeMarkerImg}/>;
          {
              this.state.open
                ? <div style={styles.popup}><Popup info={info} /></div>
                : <div />
          }
        </div>
    );
  }
}
 
export default Marker;